$collection-colors: (
  "main-bg-color": #ffff,
  "banner-bg-color": #2f303a,
  "team-bg-color": #f5f4fa,
  "footer-bg-color": #2f303a,
  "action-color": #2196f3,
  "btn-active-color": #188ce8,
  "section-title-color": #212121,
  "common-text-color": #757575,
  "social--link--color": #afb1b8,
);

$collection-shadows: (
  "modal-shadow": #{0px 1px 3px rgba(0, 0, 0, 0.12),
  0px 1px 1px rgba(0, 0, 0, 0.14),
  0px 2px 1px rgba(0, 0, 0, 0.2)},
  "card-shadow": #{0px 1px 1px rgba(0, 0, 0, 0.12),
  0px 4px 4px rgba(0, 0, 0, 0.06),
  1px 4px 6px rgba(0, 0, 0, 0.16)},
  "btn-shadow": #{0px 3px 1px rgba(0, 0, 0, 0.1),
  0px 1px 2px rgba(0, 0, 0, 0.08),
  0px 2px 2px rgba(0, 0, 0, 0.12)},
  "order-btn": #{0px 4px 4px rgba(0, 0, 0, 0.15)},
);
$collection-gaps: (
  "card-set-gap": 30px,
  "card-respons-set-gap": 30px,
);

$mobile: 320px;
$to-tablet: 767px;
$tablet: 768px;
$to-desktop: 1279px;
$desktop: 1280px;
